/// @group  Blocks
/// @author Sky Shabatura <theme@sky.camp>
/// @link   https://taproot-theme.com
////

.wp-block-pullquote {
    padding: 0;

    &.alignright,
    &.alignleft {
        max-width: 42%;
    }

    blockquote {
        margin: 0;
        border-left: 3px solid;
        padding: 0 1rem;
    }

    p {
        font-size: 1.625em;
        line-height: 1.3;
        margin: 0;
    }

    cite,
    footer {
        position: relative;
    }

    cite {
        display: inline-block;
        margin-top: 0.4em;
    }
}
